.card {
    height: 100px;
    position: relative;
    margin: 1px;
    box-shadow: 0 0 2px rgba(51, 51, 51, 0.25);
    transition: 0.3s;
    overflow: hidden;
    border-radius: 4px;
}

.card:hover {
    -webkit-transform: scale(1.25);
            transform: scale(1.25);
    z-index: 2;
}

.card img {
    object-fit: cover;
    height: 100px;
    width: 160px;
}

.card .datacontener {
    position: absolute;
    top: 0;
    opacity: 0;
    width: 100%;
    height: 100%;
    transition: 0.2s;
}

.card .datacontener:hover {
    opacity: 1;
}

.card .datacontener ul {
    color: black;
    padding-top: 5px;
    display: grid;
    height: 100%;
    width: 100%;
    text-align: center;
    transition: 0.3s;
    -webkit-transform: scaleY(0.3);
            transform: scaleY(0.3);
    z-index: 20;
}

.card .datacontener ul:hover {
    -webkit-transform: scaleY(1);
            transform: scaleY(1);
}

.card .datacontener ul:after {
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    transition: 0.02s;
}

.card .datacontener ul:hover::after {
    background: rgba(0, 0, 0, 0.65);
}

.card .datacontener ul li {
    font-size: 0.5rem;
    z-index: 30;
    list-style-type: none;
    color: white;
    text-align: start;
}

.card .datacontener ul li {
    font-size: 0.8rem;
    z-index: 30;
}

.card .datacontener ul li:nth-child(1) {
    font-weight: bold;
    color: rgba(0, 225, 255, 0.794);
}
.countries ul {
    display: block;
    display: flex;
    flex-wrap: wrap;
}

.countries .listcountries {
    display: flex;
    flex-wrap: wrap;
}

.countries .sorcontener {
    display: grid;
    grid-template-columns: 20% 80%;
    padding: 8px;
    border-radius: 15px;
    background: #282c34;
    color: #61dafb;
    box-shadow: 0 2px 2px rgba(51, 51, 51, 0.3);
}

.countries .sorcontener ul {
    display: flex;
    justify-content: space-around;
}

.countries .sorcontener ul input[type="radio"] {
    -webkit-transform: translate(-5px, 1px);
            transform: translate(-5px, 1px);
}

.countries .annuler {
    max-width: 200px;
    margin: auto;
}

.countries .annuler h5 {
    border: 1px #282c34 solid;
    text-align: center;
    width: 180px;
    margin-top: 0.5rem;
    border-radius: 10px;
    transition: 0.3s;
    cursor: pointer;
    color: #ffff;
    background: rgba(0, 0, 0, 0.616);
}

.countries .annuler h5:hover {
    background: #61dafb;
    border-color: #61dafb;
    color: black;
}

@media all and (max-width:640px) {
    .countries .sorcontener {
        max-width: 450px;
        margin: auto;
    }
    .countries .sorcontener ul {
        justify-content: inherit;
    }
    .countries .sorcontener ul {
        list-style-type: none;
        display: block;
        display: flex;
        flex-direction: column;
        /* align-items: center; */
        justify-content: flex-start;
    }
}
.article-container{
    margin: 10px 50px;
    background: white;
    box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
    border-radius: 5px;
    padding: 15px;
}
.article-container .container-auth-post
{
    display: flex;
    justify-content: space-between;
    margin: 5px 0 10px;
}
.article-container em{
    font-size: 0.8rem;

}
.article-container p{
    font-size: 0.9rem;

}
.article-container .container-btns{
    display: flex;
    justify-content: flex-end;
