    font-size: 0.7rem;
    margin: 0 2px;
    cursor: pointer;

}
.article-container .container-btns button :hover{
    -webkit-filter: brightness(110%);
            filter: brightness(110%);

}
.contener-news{
    text-align: center;
    margin-bottom: 10px;
}
.contener-news form {
    display: grid;
    min-width: 400px;
    text-align: center;
    width: 50%;
    max-width: 600px;
    margin: 0 auto;

}
.contener-news form input,
.contener-news form textarea{
    margin: 5px 0;
    padding: 5px;
    border-radius: 5px;
    border: 1px solid aqua;
    resize: none;
    box-shadow: 0 2px 6px rgba(0, 0, 0, 0.05);

}
.contener-news form textarea{
    height: 80px;

}
.contener-news form input[type="submit"]{
    width: 200px;
    margin-left: auto;
    cursor: pointer;
    transition: 0.4s;
    background: aqua;

}
.contener-news form input[type="submit"]:hover{
    background: #282c34;
    color: white;

}
